import React , { Component } from 'react';
import ReactDOM from 'react-dom';

export default class AddFriend extends Component {
    constructor(props, context) {
        super(props, context)
   
    }



    render() {
        if (this.props.hidden == true) {
            return null;
        } 

        return ReactDOM.createPortal((<div>
            <div style={{'position':'fixed','left':'25%','top':'45%'}} className="jumbotron well-sm" align ="center">
                  <p>
                    
                    Friend Nickname#id
                    <input type="text" className="form-control" />
                  </p>
                  <button type="button" className="btn btn-default" onClick={this.props.buttonOk}>Ok</button>
                  <button type="button" className="btn btn-default" onClick={this.props.buttonCancel}>Cancel</button>
                </div>
        </div>),document.getElementById('portal'))
        
    }
}