import React from 'react';
import {Link, withRouter} from "react-router-dom";
import socket from '../../core/socket';

import Alert from '../../components/b3/Alert';

import '../../style/signin.css';

class Register extends React.Component {
	static _loopAlert = null;
	constructor(props) {
		super(props);
		this.state = {
	      	dataAuth: {
		        username: '',
		        password: '',
		        email: '',
		        repassword: ''
			},
			alert: {
				show:false,
				message: 'hi',
				close: true,
				type: 'success'
			}
		}

	}

	componentDidMount() {
		socket.on('register', (data) => {
	      	console.log(data);
	      	if (data.codeStatus === 200) {
	        	this.setAlert('success', 'Register Success. We have sent a code to your inbox to validate your email. Login and enter the code', 8000)
	        	//this.goTo('/auth/login'); 
	        	var dataAuth = {...this.state.dataAuth};
			    dataAuth.username = '';
			    dataAuth.email ='';
			    dataAuth.password = '';
			    dataAuth.repassword = '';
		    	this.setState({
		      		dataAuth
		    	})     	
	      } else {
	      	this.setAlert('danger', data.message)
	      }
	    });
	}

	componentWillUnmount() {
	    var dataAuth = {...this.state.dataAuth};
	    var stateAlert = {...this.state.alert};
	    dataAuth.username = '';
	    dataAuth.email ='';
	    dataAuth.password = '';
	    dataAuth.repassword = '';
  		stateAlert.show = false;
	  	stateAlert.type = '';
	  	stateAlert.message = '';
	  	stateAlert.close = false;
    	this.setState({
      		dataAuth,
      		dataAlert:stateAlert
    	})
    	socket.off('register');
    	//socket.removeAllListeners();
	}

	goTo = (path) => {
		this.props.history.push(path);
	}

	handleInput = (event, value) => {
		var dataAuth = {...this.state.dataAuth}
	    dataAuth[value] = event.target.value;
	    this.setState({
	        dataAuth
	    })
	}

	handleAlert = (childData) => {
		//console.log(childData);
		var stateAlert = {...this.state.alert};
  		stateAlert.show = childData;
		this.setState({
	  		alert:stateAlert
	  	})
	  	clearTimeout(this._loopAlert);
	}

	setAlert = (type, message = '', time=3000, show = true, close = true) => {
		// Propagation Operation
		var stateAlert = {...this.state.alert};
  		stateAlert.show = show;
  		stateAlert.type = type;
  		stateAlert.message = message;
  		stateAlert.close = close;
	  	this.setState({
	  		alert:stateAlert
	  	})
	  	// Hide Alerts
	  	this._loopAlert = setTimeout(() => {
				var stateAlert = {...this.state.alert};
		  		stateAlert.show = false;
			  	this.setState({
			  		alert:stateAlert
			  	})
	  	}, time)
	}

	render() {
	    return (
	        <form className="form-signin">
				<h2 className="form-signin-heading"><center>Create Nova Account</center></h2>
				<Alert handleCloseAlert={this.handleAlert} show={this.state.alert.show} type={this.state.alert.type} message={this.state.alert.message} close={this.state.alert.close}/>
				<div>
					<input id="ign" value={this.state.dataAuth.ign} onChange={(event) => this.handleInput(event, 'ign')} className="form-control" type="text" placeholder="In Game Name"/>
					<input id="username" value={this.state.dataAuth.username} onChange={(event) => this.handleInput(event, 'username')} className="form-control" type="text" placeholder="Username"/>
					<input id="email" value={this.state.dataAuth.email} onChange={(event) => this.handleInput(event, 'email')} className="form-control" type="text" placeholder="Email"/>
					<input id="password" value={this.state.dataAuth.password} onChange={(event) => this.handleInput(event, 'password')} className="form-control" type="password" placeholder="Password"/>
					<input id="repassword" value={this.state.dataAuth.repassword} onChange={(event) => this.handleInput(event, 'repassword')} className="form-control" type="password" placeholder="Re-Password"/>
				</div>
				<button className="btn btn-lg btn-primary btn-block" type="button" onClick={this.doCreate} >Create</button>
				<p className="pOr">-Or-</p>
				<button className="btn loginBtn loginBtn--facebook btn-block" type="button" onClick={this.registerFacebook}>Register with Facebook</button>
				<button className=" btn loginBtn loginBtn--google btn-block" type="button" onClick={this.registerGoogle}>Register with Google</button>
				<Link to="/auth/login" className="form-control btn btn-lg btn-block">Go to Login</Link>
	        </form>
	    );
	}

	registerFacebook = () => {
		console.log('register facebook');
		//this.facebookWindow = window.open('https://www.facebook.com/v5.0/dialog/oauth?client_id=2715064835200025&redirect_uri=http://localhost:8080&state=state-param&response_type=token&display=popup','Oauth Facebook', "width=600,height=400,left=200,top=200");
	}

	registerGoogle = () => {
		console.log('login google ready');
	}

	doCreate = () => {
		let dataAuth = this.state.dataAuth;
	    if (dataAuth.username.trim() === '' ||
          	dataAuth.email.trim() === '' ||
          	dataAuth.password.trim() === '' ||
          	dataAuth.repassword.trim() === ''
          	) 
	    {
	        this.setAlert('danger', 'Inalid data');
	    } else {
	        if (dataAuth.password !== dataAuth.repassword) {
	         	this.setAlert('warning', 'Password Not Match');
	        } else {
	        	//this.setAlert('success', 'To create');
	          	socket.emit('register', this.state.dataAuth);
	        }
	    }
	}
}

export default withRouter(Register);