import React from 'react';
import {
  MemoryRouter as Router,
  Switch,
  Route,
  //Link
} from 'react-router-dom';



// Import Views and components
import Login from './views/auth/login';
import Register from './views/auth/register';
import RecoverPass from './views/auth/recover_pass';
import Main from './views/main/main';
//import ValidateEmail from './views/main/validate_email/validate_email';
//import FirstScreen from './views/main/first_screen/first_screen';
// Import Globals Css
import './App.css';

function App() {
	return (
		<div>
			{/*
				<h2>Hello on Home</h2>
				<p>Welcome to React App</p>
				<Link to="/">Home</Link> <br/>
				<Link to="/about">About</Link> <br/>
				<Link to="/auth/register">Register</Link> <br/>
			*/}
			<Router>
				<Switch>
					<Route exact path="/">
						<Login></Login>
					</Route>
					<Route path="/main" component={Main}></Route>
					{/*Routes for Auth */}
					<Route path="/auth/login" component={Login}></Route>
					<Route path="/auth/register" component={Register}></Route>
					<Route path="/auth/recoverpass"><RecoverPass></RecoverPass></Route>
					{/*<Route path="/validate/email" component={ValidateEmail}></Route>
					<Route path="/validate/nickname" component={FirstScreen}></Route>*/}
				</Switch>
			</Router>
		</div>
	)
}

