import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import FloatMessage from '../../components/FloatMessage/FloatMessage'
//import $ from 'jquery'
import socket from '../../core/socket';

import { Button } from 'react-bootstrap';

import {Link,withRouter} from 'react-router-dom';

import '../../style/offcanvas.css';

import {Modal} from 'react-bootstrap';

import './main.css';
// sub elements
import RoomList from './roomList';
import Room from './room';
import ChampionList from './championList';
import EndGame from './EndGame'

import Chat from './chat';
import FriendList from './friendList';

class Main extends Component {

    constructor(props, context) {
      super(props, context);
      this.handleShow = this.handleShow.bind(this);
      this.handleClose = this.handleClose.bind(this);
      // toys for childrens
      this.goTo = this.goTo.bind(this);
      this.Navbar_enabled.bind(this);
      this.Navbar_disabled.bind(this);
      //this.goTo_Room = this.goTo_Room.bind(this);
      //this.goTo_RoomList = this.goTo_RoomList.bind(this);
      
      this.state = {
        navbar_status:true,
        show: false,
        type: "list",
        dataUser: this.props.location.state.dataUser,
        roomData: undefined,
        channel:1
      };

    
    }

    disconnect = () => {
      //this.props.history.push('/');	this is giving a error because we are doing this twice
      console.log("going back to login ...");
    }

    handleClose() {
      this.setState({ show: false });
    }
  
    handleShow() {
      this.setState({ show: true });
    }

    renderType() {
      switch(this.state.type) {
        case "list":
          return(<RoomList roomLink={this.goTo} dataUser={this.state.dataUser} />);
        case "room":
          return(<Room listLink={this.goTo}  roomData={this.state.roomData} dataUser={this.state.dataUser} />);
        case "champion":
          return(<ChampionList roomLink={this.goTo} roomData={this.state.roomData} dataUser={this.state.dataUser} Navbar_setState={{enabled: this.Navbar_enabled, disabled: this.Navbar_disabled}}/>);
        case "end-game":
          return(<EndGame roomLink={this.goTo} dataUser={this.state.dataUser}  />)  
      }
    }

    goTo_Room() {
      console.log("going to room");
      this.setState({type:"room"});
    }

    goTo(where, data=undefined) {
      this.setState({type:where, roomData:data});
    }

    goTo_RoomList() {
      this.setState({type:"list"});
    }

    Navbar_enabled = () => {
      this.setState({
        navbar_status:true
      })
    }

    Navbar_disabled = () => {
      this.setState({
        navbar_status:false
      })
    }


    Navbar() {
      if (this.state.navbar_status) {
        return(<nav className="navbar navbar-fixed-top navbar-inverse">
        <div className="container">
          <div className="navbar-header">
            <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
              <span className="sr-only">Toggle navigation</span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
            </button>
            <a className="navbar-brand" href="#/">NOVA</a>
          </div>
          <div id="navbar" className="collapse navbar-collapse">
            <ul className="nav navbar-nav">
              <li className="active"><a href="/#/">Rooms</a></li>
              <li><Link to = "/" onClick={this.disconnect}>Disconnect <span className="glyphicon glyphicon-off"></span></Link></li>
            </ul>
          </div>
        </div>
      </nav>)
      } else return null;
    }

    render() {
        return (
        <div>
          
          {this.Navbar()}
      
          <div className="container">
            
            <div className="row row-offcanvas row-offcanvas-right">
              <div>
              {this.renderType()}
              </div>
      
              <div className="col-xs-6 col-sm-3 sidebar-offcanvas" id="sidebar">
                <div className="list-group">
                  <a className="list-group-item active"><h4>{this.state.dataUser.name}</h4><h4>Lvl {this.state.dataUser.level}</h4>Friend List <button type="button" className="btn-default"><span className="glyphicon glyphicon-plus" /><span className="glyphicon glyphicon-user" /></button></a>
                  <FriendList />
                </div>
              </div>
            </div>
      
          </div>

          <Chat user={this.state.dataUser.name} channel={this.state.channel}/>
          
          
        </div>
        );


    }

    
}



export default withRouter(Main);