import React, { Component, forwardRef, useImperativeHandle } from 'react';
//import $ from 'jquery'
import socket from '../../core/socket';

class Chat extends Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            messages: [
                {
                    name: "[Server]",
                    channel:"global",
                    text: "Bienvenido a Nova"
                }
            ],
            owner: this.props.user,
            text: "",
            channel:0
        }
        this.myRef = React.createRef();
    }

    componentDidMount() {
      console.log("mounting chat")
      socket.on('chat', (data) => {

        console.log(data);
        this.newMessage(data.user, data.text);
        
      })
      socket.on('join-room', (data) => {
        console.log("join-room",data)
        //this.setState({channel: data})
      })
    }

    messages() {
        return(
            <div>
            {this.state.messages.map(function(value, index) {
                return (
                <p className="msg">{value.name} : {value.text}</p>
                )
                })}
            </div>
        );
    }

    newMessage(owner, text) {
        this.state.messages.push({
            name:owner,
            text:text
        });

        if (this.state.messages.length > 10) {
          this.state.messages.shift();
        } 

        this.setState({
            messages:this.state.messages
        })

        
    }

    inputMessage() {
        //console.log(this.state.text)
        //this.newMessage(this.state.owner, this.state.text) // local call back
        socket.emit('chat', {user: this.state.owner, text: this.state.text, channel:this.state.channel})
        //this.state.text = "";
        this.setState({
          text:""
        })
    }

    handleInput = (event) => {
		//var values = {...this.state.dataAuth}
	    //dataAuth[value] = event.target.value;
	    this.setState({
	        text:event.target.value
	    })
	}

    render() {
      return (
        <div>
          <div className="col-lg-6 chat-messages" id="chat-messages">
            {this.messages()}
          </div>
          <div className="col-lg-6 chat-inputer">
            <div className="input-group">
              <input type="text" value = {this.state.text} onChange={(event) => this.handleInput(event)} className="form-control input-messager" placeholder="Click to chat" id="input-sent-message" />
              <span className="input-group-btn">
                <button className="btn btn-default" type="button" onClick={() => this.inputMessage()}><span className="glyphicon glyphicon-play-circle" /></button>
              </span>
            </div>
          </div>
        </div>
      );
    }
  }


  export default Chat;