import React, { Component } from 'react';
import { ButtonToolbar, DropdownButton } from 'react-bootstrap';
import socket from '../../core/socket';
import ReactDOM from 'react-dom';
import FloatMessage from '../../components/FloatMessage/FloatMessage';
import QuestionYesNo from '../../components/FloatMessage/QuestionYesNo';

class Room extends Component {

      constructor(props, context) {
          super(props, context)

          this.state = {
            roomId:-1,
            creator:"",
            gred: [
              /*{
                id:0,
                name: "Klys",
                level: 7,
                champion: ""
              },
              {
                id:0,
                name: "Sylk",
                level: 6,
                champion: ""
              },
              {
                id:0,
                name: "Zeryka",
                level: 5,
                champion: ""
              },
              {
                id:0,
                name: "Plop",
                level: 15,
                champion: ""
              },
              {
                id:0,
                name: "HolaATodos",
                level: 20,
                champion: ""
              }*/
            ],
            gblue: [
              /*{
                id:0,
                name: "Nope",
                level: 9,
                champion: ""
              },
              {
                id:0,
                name: "Orale",
                level: 15,
                champion: ""
              },
              {
                id:0,
                name: "Miser",
                level: 15,
                champion: ""
              },
              {
                id:0,
                name: "Cord",
                level: 15,
                champion: ""
              },
              {
                id:0,
                name: "Iaaah",
                level: 15,
                champion: ""
              }*/
            ]
          }


          if (this.props.roomData.roomData !== undefined) {
            this.props.roomData.roomData.map((player) => {
              switch(player.team) {
                case 100:
                  this.state.gred.push(player)
                break;
                case 200:
                  this.state.gblue.push(player)
                break;
              }
            });
            this.state.roomId = this.props.roomData.roomId;
            this.state.creator = this.props.roomData.creator;
          } else {
            this.state.roomId = this.props.roomData;
            socket.emit("room-data-request", {roomId:this.state.roomId});
          }

          
          this.state.fmessage= {
            message:"",
            hidden:true
          }

          this.state.start = {
            question:"Los grupos estan desiguales, deseas comenzar la partida con esta imparidad?",
            hidden:true
          }

          this.buttonStartYes = this.buttonStartYes.bind(this);
          this.buttonNo = this.buttonNo.bind(this);
          this.buttonOk = this.buttonOk.bind(this);
          
          
      }


      componentDidMount() {
        socket.on("room-data-update", (data) => {
          console.log(data)
          //var players = JSON.parse(data[0].data)
          //console.log(players)
          var gred = [];
          var gblue = [];
          data.map((player) => {
            switch(player.team) {
              case 100:
                gred.push(player)
                //this.state.gred.push(player);
              break;
              case 200:
                gblue.push(player)
                //this.state.gblue.push(player);
              break;
            }
          })
          this.setState({gred:gred, gblue:gblue})
        })


        socket.on('room-champion-selection-enter', (data) => {
          this.props.listLink('champion', {gblue:this.state.gblue,gred:this.state.gred,champions:data, roomId:this.state.roomId})
        })

      }

      othersOptions(name) {
        if (this.props.dataUser.name != name)
        if (this.state.creator == this.props.dataUser.name) {
          return(<React.Fragment>
            <li><a onClick={() => this.KickPlayer()}>Expulsar</a></li>
            <li><a href="#">Seder Sala</a></li>
          </React.Fragment>)
        }
        
      }

      KickPlayer() {
        console.log("kick player...")
      }

      groupPlayers(players) {
        return(
          <div>
          {players.map((value, index) => {
            return (
              <div className="panel-body">
                      <div className="col-xs-6">
                        <h4>{value.name} lvl {value.level}</h4>
                      </div>
                      <div className="col-xs-6">
                        <div className="dropdown" align="right">
                          <button className="btn btn-default dropdown-toggle" type="button" id="dropdownMenu1" data-toggle="dropdown" aria-haspopup="true" aria-expanded="true">
                            <span className="glyphicon glyphicon-cog" />
                            <span className="caret" />
                          </button>
                          <ul className="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenu1">
                            {this.othersOptions(value.name)}
                            <li><a href="#">Agregar</a></li>
                          </ul>
                        </div>
                      </div>
                    </div> 
        ) })}
        </div>)

      }

      exitRoom() {
        if (this.state.creator === this.props.dataUser.name) {
          // if we are the owner of the room
          // then we check if there is more players on the room
          if ((this.state.gblue.length >= 1) || (this.state.gred.length >= 1)) {
            
          }
        }
        socket.emit('room-exit')
        this.props.listLink('list')
      }

      closeRoom() {

      }

      startRoom() {
        if (this.state.gblue.length != this.state.gred.length) {
            this.setState({
              start: {
                question:"Los grupos estan desiguales, deseas comenzar la partida con esta imparidad?",
                hidden:false
              }
            })
          return -1;
        }

        socket.emit("room-start-intent", {roomId:this.state.roomId})
        
        //this.props.listLink('champion')
      }

      switchTeam() {
        console.log(this.state.roomId)
        socket.emit("switch-team-request", {roomId:this.state.roomId})
      }

      permissionButtons() {
        if (this.state.creator === this.props.dataUser.name) {
          return(<React.Fragment>
            <button className="btn btn-default" type="submit" onClick={() => this.closeRoom()} >Close</button>
            <button className="btn btn-default" type="submit" onClick={() => this.startRoom()} >Start</button>
          </React.Fragment>)
        } else {
          return (<React.Fragment>
            <button className="btn btn-default" type="submit" onClick={() => this.exitRoom()}   >Exit</button>
          </React.Fragment>)
        }
      }

      // FloatMessage ButtonOk

      buttonOk() {
        this.setState({
          fmessage: {
            hidden:true
          }
        })
      }

      // QuestionYesNo Start Buttons:

      buttonNo() {
        this.setState({
          start: {
            hidden:true
          }
        })
      }

      buttonStartYes() {
        this.setState({
          start: {
            hidden:true
          }
        })
        socket.emit("room-start-intent", {roomId:this.state.roomId})
      }

        render() {
            return (
              <div className="col-xs-12 col-sm-9">
                <p className="pull-right visible-xs">
                  <button type="button" className="btn btn-primary btn-xs" data-toggle="offcanvas">Toggle nav</button>
                </p>
                <div className="jumbotron well-sm">
                  <p>
                    {this.permissionButtons()}
                    <button className="btn btn-default" type="submit" onClick={() => this.switchTeam()} >Pastelear</button>
                  </p>
                </div>
                <div className="row">
                  <div className="panel panel-info col-xs-6 col-lg-6"> 
                    <div className="panel-heading"> 
                      <h3 className="panel-title">Grupo Azul</h3> 
                    </div> 
                    {this.groupPlayers(this.state.gblue)}
                      
                    </div> 
                  
                  <div className="panel panel-danger col-xs-6 col-lg-6"> 
                    <div className="panel-heading"> 
                      <h3 className="panel-title">Grupo Rojo</h3> 
                    </div> 
                    {this.groupPlayers(this.state.gred)}
                      
                    </div> 
                  </div>
                  <FloatMessage message={this.state.fmessage.message} hidden={this.state.fmessage.hidden} buttonOk={this.buttonOk}/>
                  <QuestionYesNo question={this.state.start.question} hidden={this.state.start.hidden} buttonNo={this.buttonNo} buttonYes={this.buttonStartYes} />
                </div>
                          
            );
        }
}

export default Room;