import React, { Component } from 'react';
import socket from '../../core/socket';

class RoomList extends Component {

    constructor(props, context) {
        super(props, context)
        this.state = {
          dataUser:this.props.dataUser,
          nRooms: [{creator:'Jake', users:4,game:'Moba'}]
        };
        socket.emit('room-list-request')
      }

      //  <button onClick={() => this.refreshRooms()} className="btn btn-default" type="button">Refresh<span className="glyphicon glyphicon-refresh"></span></button>
    render() {
        return(
            <div className="col-xs-12 col-sm-9">
                <p className="pull-right visible-xs">
                  <button type="button" className="btn btn-primary btn-xs" data-toggle="offcanvas">Toggle nav</button>
                </p>
                <div className="jumbotron well-sm">
                  <h3>Crea un partida e invita a tus amigos, o unete</h3>
                  <p>
                    <button onClick={() => this.createRoom()} className="btn btn-default" type="button">Crear partida <span className="glyphicon glyphicon-plus"></span></button>
                  </p>
                </div>
                
            {this.roomTabs(this)}
                  
                </div>
        );
    }

    createRoom() {
        //var arr = this.state.nRooms;
        //arr.push('Algo');
        //this.setState({ nRooms:[] });
        socket.emit('room-create-intent')
      }

      refreshRooms() {
        socket.emit('room-list-request')
      }

      componentDidMount() {
        socket.on('room-create-success', (data) => {
          this.props.roomLink('room', data);
        })
        
        socket.on('room-list-update', (data) => {
          console.log("updating list of rooms")
          console.log(data)
          //this.state.nRooms = data;
          this.setState({nRooms:data})
        })

        socket.on("room-enter-accepted", (data) => {
          this.props.roomLink('room', data)
        })

        socket.on("room-enter-denied", (data) => {

        })


      }


      enterRoom(roomId) {
        socket.emit('room-enter-request', {room:roomId})
        //this.props.roomLink('room', roomId)
      }

      

    roomTabs(parent) {

        //var nRooms = ['Jake', 'Jon', 'Thruster'];
          return (
            <div className="row">
              {this.state.nRooms.map((value, index) => {
                return (
                  
                      <div className="col-xs-6 col-lg-4 well btn-default" style={{height: 150}} align = "center" onClick={() => this.enterRoom(value.id)}>
                              
                              <h2>{value.creator}</h2>
                              <p>{value.game}</p>
                              <p><span className = "glyphicon glyphicon-user"></span> {value.users}/10</p>
                              
                      </div>
                            
                  
                )
              })}
            </div>
        );
        
        
      }
}


export default RoomList;