import React , { Component } from 'react';
import ReactDOM from 'react-dom';

export default class ChatBox extends Component {
    constructor(props, context) {
        super(props, context)
        this.state = {
            x:window.innerWidth*0.25,
            y:window.innerHeight*0.25,
            dragging:false,
            mx:0,my:0
        }
    }

    startDragginBox() {
        this.setState({
            dragging:true
        })
    }

    dragginBox(e) {
        if (this.state.dragging == false) return;
        this.setState({ x: e.screenX-100, y: e.screenY-100 });
        console.log("Dragging the box. x"+this.state.x+", y"+this.state.y)
    }

    droppingBox() {
        console.log("Dropping the box")
        this.setState({
            dragging:false
        })
    }

    render() {
        if (this.props.hidden == true) {
            return null;
        } 

        return ReactDOM.createPortal((<div>
            <div style={{'position':'fixed','left':this.state.x,'top':this.state.y, 'width':'230px'}} className="jumbotron well-sm" align ="center">
                <div className="panel-heading btn-default" onMouseDown={this.startDragginBox.bind(this)} onMouseUp={ this.droppingBox.bind(this)} onMouseOver={this.dragginBox.bind(this)}> 
                      <h3 className="panel-title">Chatting with: This</h3> 
                    </div> 
                  <div style={{'height':'150px'}} align="left">
                  This:messages<br/>This:messages<br/>This:messages<br/>This:messages<br/>This:messages<br/>This:messages<br/>
                  </div>

                  <div className="col-lg-12">
            <div className="input-group">
              <input type="text" className="form-control input-messager" placeholder="Click to chat" />
              <span className="input-group-btn">
                <button className="btn btn-default" type="button"><span className="glyphicon glyphicon-play-circle" /></button>
              </span>
            </div>
          </div>
                </div>
        </div>),document.getElementById('portal'))
        
    }
}