import React, { Component } from 'react';
import {Link, withRouter } from "react-router-dom";
//import {Alert} from 'reactstrap'
//import { Button } from 'reactstrap';
import Alerts from '../../components/Alerts';

import Alert from '../../components/b3/Alert';
import '../../style/signin.css';

// socket Import
import socket from '../../core/socket';


class Login extends Component {
	static _loopAlert = null;
	static _isMounted = null;
	static facebookWindow = null;
	constructor(props) {
		super(props);
		this.state = {
			dataUser:{
				name:'',
				id:-1
			},
	      	dataAuth: {
		        username: '',
		        password: ''
			},
			alert: {
				show:false,
				message: 'hi',
				close: true,
				type: 'success'
			}
		}
	}

	componentDidMount() {
		console.log("login mounted")
		this._isMounted = true;
		window.addEventListener('message', this.eventChildFB);
		socket.on('login', (data) => {
			  console.log("login data:");
			  console.log(data)
	      	switch (data.codeStatus) {
				  case 200:
	        	var dataAuth = {...this.state.dataAuth};
	        	//dataAuth.username = data.data[0].username;
				//dataAuth.email = data.data[0].email;
				dataAuth.username = data.data.username;
				dataAuth.email = data.data.email
				var dataUser = data.data;
				console.log(dataUser)
	        	/*this.setState({
					  dataAuth
	        	});*/
	        	//console.log(this.state.dataAuth);
				this.props.history.push({
					pathname: '/main',
					//search: '?query=abc',
					state: { dataUser} 
				  });
				console.log("going to main")
				break;	        	
	      /*} else {
	      	this.setAlert('danger', data.message, true)
				this.props.history.push('/main');      **/  	
	      case 401:
			  this.goTo('/validate/email', data.data); // need to be checked
			  break;
	      case 4012:
			  this.goTo('/validate/nickname', data.data); // need to be checked
			  break;
	      default:
	      	this.setAlert('danger', data.message);
		  } // end of switch
		  

		});


		
	    socket.on('authFacebook', (data) => {
	    	console.log('authFacebook', data)
	    	if (data.codeStatus === 200) {
	    		this.goTo('/main')
	    		this.setAlert('success', data.message, 7000);
	    	} else if(data.codeStatus === 4012) {
		      	this.goTo('/validate/nickname', data.data[0]);
		    }else {
	    		this.setAlert('danger', data.message);
	    	}
	    })
	}

	componentWillUnmount() {
		this._isMounted = false;
	    var dataAuth = {...this.state.dataAuth};
	    var stateAlert = {...this.state.alert};
	    dataAuth.username = '';
	    dataAuth.email ='';
  		stateAlert.show = false;
	  	stateAlert.type = '';
	  	stateAlert.message = '';
	  	stateAlert.close = false;
    	this.setState({
      		dataAuth,
      		alert:stateAlert
    	})
    	socket.off('login');
    	socket.off('authFacebook');
    	if (this.facebookWindow != null) {
    		this.facebookWindow.close();
    	}
    	if (this._loopAlert != null) {
    		clearTimeout(this._loopAlert);
    	}
    	//socket.removeAllListeners();
    	window.removeEventListener('message', this.eventChildFB);
	}
	render() {
	    return (
	        <form className="form-signin">
				<h2 className="form-signin-heading"><center>Nova </center></h2>
				{/* <Alert variant="success" isOpen={this.state.visible} color="info">
			    	Exito brother
			  	</Alert>*/}
			  	<Alert handleCloseAlert={this.handleAlert} show={this.state.alert.show} type={this.state.alert.type} message={this.state.alert.message} close={this.state.alert.close}/>
				<div>
					<input id="username" value={this.state.dataAuth.username} onChange={(event) => this.handleInput(event, 'username')} className="form-control" type="text" placeholder="Username or Email"/>
					<input id="password" value={this.state.dataAuth.password} onChange={(event) => this.handleInput(event, 'password')} className="form-control" type="password" placeholder="Password"/>
				</div>
			      
				<button className="btn btn-lg btn-primary btn-block" type="button" onClick={this.doLogin} >Conectarse</button>
				{/*
				<GoogleLogin
					className="form-control btn-default"
				    clientId="121916204697-lrg44ergi7h32f1a6bor4sh7rbt8k0ru.apps.googleusercontent.com"
				    buttonText="Login"
				    onSuccess={this.responseGoogle}
				    onFailure={this.responseGoogle}
				    cookiePolicy={'single_host_origin'}
				  />
				*/}
				
				<button className="btn loginBtn loginBtn--facebook btn-block" type="button" onClick={this.loginFacebook}>Entrar con Facebook</button>
				<button className=" btn loginBtn loginBtn--google btn-block" type="button" onClick={this.loginGoogle}>Entrar con Google</button>
				<p className="pOr">-Or-</p>
				<Link to="/auth/register" className="form-control">Crear Cuenta</Link>
				<Link to="/auth/recoverpass" className="form-control">Recuperar Clave</Link>
				<a className='form-control' href="javascript:Android.close()">Close Test 1</a>
				<a className='form-control' href="javascript:Android.closeWebview()">Close Test 2</a>
				<a className='form-control' href="javascript:Android.closeThis()">Close Test 3</a>
				<a className='form-control' href="http://exit_me_now" target="_self">Close Test 4</a>
	        </form>
	    );
	}

	handleAlert = (childData) => {
		//console.log(childData);
		var stateAlert = {...this.state.alert};
  		stateAlert.show = childData;
		this.setState({
	  		alert:stateAlert
	  	})
	  	clearTimeout(this._loopAlert);
	}

  	doLogin = () => {
  		clearTimeout(this._loopAlert);
  		var dataUser = this.state.dataAuth;
  		if (dataUser.username.trim() === '' || dataUser.password.trim() === '') {
  			this.setAlert('danger', 'Invalid data');
  		} else {
  			this.setAlert('info', 'Processing data');
  			socket.emit('login', this.state.dataAuth);
  		}
	}

	goTo = (path, data = null) => {
		this.props.history.push({pathname:path, state: data});
	}

	loginFacebook = () => {
		this.facebookWindow = window.open('https://www.facebook.com/v5.0/dialog/oauth?client_id=2715064835200025&redirect_uri=http://localhost:8080&state=state-param&response_type=token&display=popup','Oauth Facebook', "width=600,height=400,left=200,top=200");
	}

	loginGoogle = () => {
		console.log('login google ready');
	}

	eventChildFB = (e) => {
		if (e.origin === 'http://localhost:8080') {
			console.log(e.data);
			if (this.facebookWindow != null) {
	    		this.facebookWindow.close();
	    	}
			localStorage.setItem('email', e.data.email);
			socket.emit('authFacebook', e.data);
			//this.goTo('/main');
		}
	}

	handleInput = (event, value) => {
		var dataAuth = {...this.state.dataAuth}
	    dataAuth[value] = event.target.value;
	    this.setState({
	        dataAuth
	    })
	}

	setAlert = (type, message = '', show = true, close = true) => {
		// Propagation Operation
		if (this._isMounted) {
			var alert = {...this.state.alert};
	  		alert.show = show;
	  		alert.type = type;
	  		alert.message = message;
	  		alert.close = close;
		  	this.setState({
		  		alert:alert
		  	})
		  	// Hide Alerts
		  	this._loopAlert = setTimeout(() => {
					var alert = {...this.state.alert};
			  		alert.show = false;
				  	alert.type = '';
				  	alert.message = '';
				  	alert.close = false;
				  	this.setState({
				  		alert:alert
				  	})
		  	}, 3000)
		}

	}
}

export default withRouter(Login);