import React, {Component} from 'react';
import champPic from '../../imgs/profile.png';
//import playerPic from '../../imgs/player.svg';
import loading from '../../imgs/choosing_champion0.gif';
import socket from '../../core/socket';
import fs from "fs";

class ChampionList extends Component {

    constructor(props, context) {
      super(props, context)

      this.state = {
        time:30,
        championSelected:-1,
        championSelectedData:{
          id:0,
          name: "ChampionUndefined",
          img:loading,
          rol:"Unknow"
        },
        championDone:false,
        gred: [
          {
            id:0,
            name: "Klys",
            level: 7,
            champion: ""
          },
          {
            id:0,
            name: "Sylk",
            level: 6,
            champion: ""
          },
          {
            id:0,
            name: "Zeryka",
            level: 5,
            champion: ""
          },
          {
            id:0,
            name: "Plop",
            level: 15,
            champion: ""
          },
          {
            id:0,
            name: "HolaATodos",
            level: 20,
            champion: ""
          }
        ],
        gblue: [
          {
            id:0,
            name: "Nope",
            level: 9,
            champion: ""
          },
          {
            id:0,
            name: "Orale",
            level: 15,
            champion: ""
          },
          {
            id:0,
            name: "Miser",
            level: 15,
            champion: ""
          },
          {
            id:0,
            name: "Cord",
            level: 15,
            champion: ""
          },
          {
            id:0,
            name: "Iaaah",
            level: 15,
            champion: ""
          }
        ],
        champions: [
          {
            id:0,
            name: "Test Champ",
            img:champPic,
            rol:"adc"
          },
          {
            id:0,
            name: "Testia",
            img:champPic,
            rol:"Tank"
          },
          {
            id:0,
            name: "Tester",
            img:champPic,
            rol:"Asesino"
          },
          {
            id:0,
            name: "Testino",
            img:champPic,
            rol:"Luchador"
          }
        ]
      }

      this.state.gblue = this.props.roomData.gblue;
      this.state.gred = this.props.roomData.gred;
      this.state.champions = this.props.roomData.champions
      this.state.roomId = this.props.roomData.roomId;

      this.props.Navbar_setState.disabled()
      this.runningTime()

      //this.championData = this.championData.bind(this)
    }

    componentDidMount() {

      socket.on('room-player-champion-selected', (data) => {
        //console.log("room player champion selected", data)
        this.state.gblue.forEach((player, index) => {
          //console.log("player: ",player)
          if (player.uid == data.playerId) {

            this.state.gblue[index].image = this.championData(data.champion).image;
            //console.log("player gblue: ",this.state.gblue[index])
            this.setState({
              gblue:this.state.gblue
            })
            
          }
        })
        this.state.gred.forEach((player, index) => {
          //console.log("player: ",player)
          if (player.uid == data.playerId) {
            
            this.state.gred[index].image = this.championData(data.champion).image;
            //console.log("player gred: ",this.state.gred[index] )
            this.setState({
              gred:this.state.gred
            });

          }
        })
      });

      socket.on('room-game-start', (data) => {
        /*
          Date needed to start game:
          IP
          PORT
          Name
          Team
          Champion ID
        */
          alert("room-game-start!")

          alert(JSON.stringify(data))

          alert(this.props.dataUser.name)

          alert(this.foundMyTeam())

          alert(this.state.championSelectedData.id)

          var datax = JSON.stringify({
            game:data.game, 
            slot:data.slot,
            player:this.props.dataUser.name,
            team:this.foundMyTeam(),
            champion:this.state.championSelectedData.id
          })

          alert(datax)

          
          //const fs = require('fs');
          alert(fs)
          fs.writeFile('./datax', datax, function (err) {
            if (err) return alert(err);
            alert('data > datax');
          });
        this.props.roomLink('end-game')
        this.runGame()
        
      });

    }

    foundMyTeam() {
      
      for (var i = 0; i < this.state.gblue.length; i++) {
        if (this.state.gblue[i].name == this.props.dataUser.name) {
            return 200;
        }
      }

      for (var i = 0; i < this.state.gred.length; i++) {
        if (this.state.gred[i].name == this.props.dataUser.name) {
            return 100;
        }
      }

      return undefined;
    }


    championData(championId) {
      var forEachProxy = undefined;
      this.state.champions.forEach((champion, index) => {
        if (champion.id == championId) {
          forEachProxy = champion;
        }
      })
      return forEachProxy;
    }


    runningTime() {
      setTimeout(() => {
        this.setState({
          time:this.state.time-1
        })
        this.runningTime()
      }, 1000)
    }


    runGame() {
      alert("running the game.")
      const fs = require('fs')

      const path = './Nova.exe'

      try {
        if (fs.existsSync(path)) {
          //file exists
          alert("exists!!")
          const { execFile } = require('child_process');
          execFile('./Nova.exe')
        }
      } catch(err) {
        alert(err)
        console.error(err)
      }
      
    }


    teamates(players) {
      
      return(
        <div>
        {players.map(function(value, index) {
    
          //console.log(value.image)
          if (value.image == undefined) {
            value.image = loading;
          } 
          return (
          <div className="media" style={{height: '64px'}}>
            <div className="media-left">
              <img className="media-object" src={value.image} alt="..." width="64" heigh="64"/>
              <div style={{position: 'relative', top: '-20px'}}>{value.name}</div>
            </div>
          </div>
          )})}
          </div>
      )
    }

    


    champions() {
      return(
        <div>
        {this.state.champions.map((value, index) => {
          if (this.state.championSelected == index) {
            return (
              <div className="col-xs-3 col-lg-3 well-sm btn-warning champion" style={{height: '150px', backgroundImage: "url("+value.image+")", backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: '100%'}} align="center" data-room-button>
                <h4 style={{color: 'white'}}>{value.name}</h4>
                <p style={{color: 'white'}}>{value.rol}</p>
              </div>
              )  
          } else
            return (
            <div onClick={() => this.championClick(index)} className="col-xs-3 col-lg-3 well-sm btn-default champion" style={{height: '150px', backgroundImage: "url("+value.image+")", backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: '100%'}} align="center" data-room-button>
              <h4 style={{color: 'white'}}>{value.name}</h4>
              <p style={{color: 'white'}}>{value.rol}</p>
            </div>
            )})}
          </div>
      )
    }


    championClick(index) {
      const championData = this.state.champions[index]
      console.log("championData",championData)
      this.setState({
        championSelected:index,
        championSelectedData:championData
      })
      socket.emit('room-champion-selection',{roomId:this.state.roomId,champion:this.state.champions[index].id})
    }

    completeChampionSelection() {
      this.setState({
        championDone:true
      })
      socket.emit('room-champion-selection-player-done', {roomId:this.state.roomId})
      
    }


    timer() {
      return(
        <div className="float" style={{zIndex:9999}}>
        <h3>{this.state.time}</h3>
      </div>
      )
    }

    buttonChampion_section(done) {
      if (!done) {
        return(<React.Fragment>
          <h3>Selecciona campeon</h3>
          <p align="center">
            <button className="btn btn-default" type="submit" onClick={() => this.completeChampionSelection()}>Seleccionar </button>
          </p>
        </React.Fragment>)
      } else {
        return(
          <h3>{this.state.championSelectedData.name}</h3>
        )
      }
    }


    championList_section(done) {
      if (!done) {
        return this.champions();
      } else {
        return (<React.Fragment>
          <img src={this.state.championSelectedData.image} width="100%" height="100%"/>
        </React.Fragment>)
      }
    }


    render() {
        return(
<div>
  
    
      <div className="col-xs-12 col-sm-9" style={{textAlign: 'center'}}>
        <p className="pull-right visible-xs">
        </p>

        <div className="jumbotron well-sm" style={{marginBottom: '0px', paddingBottom: '0px', paddingTop: '0px', position: 'fixed', top: '30px', left: '15px', zIndex: 90, width: '70%', height: '70px'}} align="center">
          {this.buttonChampion_section(this.state.championDone)}
        </div>


        <div className="col-xs-2 col-lg-2" style={{position: 'fixed', top: '120px', left: '30px'}} align="center">
          Equipo Azul
            {this.teamates(this.state.gblue)}
            
          </div>
        


        <div className="col-xs-8 col-lg-8" style={{display: 'inline-block', float: 'none', marginTop: '90px'}}>
          
          {this.championList_section(this.state.championDone)}
           
        </div>



        <div className="col-xs-2 col-lg-2" style={{position: 'fixed', right: '260px', top: '120px'}} align="center">
          Equipo Rojo
          {this.teamates(this.state.gred)}
            
        </div>
      </div>
      
    
  
  
  {this.timer()}


</div>

        );
    }
}

export default ChampionList;