import React, {Component} from 'react';
import socket from '../../core/socket';

class EndGame extends Component {

    constructor(props, context) {
      super(props, context)

      this.state = {
      }

      this.state.gblue = this.props.roomData.gblue;
      this.state.gred = this.props.roomData.gred;
      this.state.roomId = this.props.roomData.roomId;


      //this.championData = this.championData.bind(this)
    }

    componentDidMount() {
      
      socket.on('room-game-end', (data) => {
        this.props.roomLink('list');
      });

    }


    render() {
return(
<div>
  
    
      <div className="col-xs-12 col-sm-9" style={{textAlign: 'center'}}>
        <p className="pull-right visible-xs">
        </p>

        <div className="jumbotron well-sm" style={{marginBottom: '0px', paddingBottom: '0px', paddingTop: '0px', position: 'fixed', top: '30px', left: '15px', zIndex: 90, width: '70%', height: '70px'}} align="center">
          <h1>Juego en progreso</h1>
        </div>


        
      </div>
      
    
  
  
  {this.timer()}


</div>

        );
    }
}

export default EndGame;