import React , { Component } from 'react';
import ReactDOM from 'react-dom';

export default class FloatMessage extends Component {
    constructor(props, context) {
        super(props, context)

        
        
    }



    render() {
        if (this.props.hidden == true) {
            return null;
        } 

        return ReactDOM.createPortal((<div>
            <div style={{'position':'fixed','left':'25%','top':'35%', 'borderColor':'white', 'borderStyle':'solid'}} className="jumbotron well-sm" align ="center">
                  <p>
                    
                    {this.props.message}
                  </p>
                  <button type="button" className="btn btn-default" onClick={this.props.buttonOk}>Ok</button>
                </div>
        </div>),document.getElementById('portal'))
        
    }
}