import React, { Component } from 'react';
import AddFriend from '../../components/FloatMessage/AddFriend';
import ChatBox from '../../components/FloatMessage/ChatBox';
//import $ from 'jquery'
import socket from '../../core/socket';

class FriendList extends Component{
    constructor(props, context) {
        super(props, context)

        this.state = {
            list: [
                {
                    id:0,
                    name:"Zeryca",
                    status: 1 // 1 Online, 0 Offline
                },
                {
                    id:1,
                    name:"Blacky",
                    status: 1 // 1 Online, 0 Offline
                },
                {
                    id:2,
                    name:"Bersnard",
                    status: 0 // 1 Online, 0 Offline
                },
                {
                    id:3,
                    name:"GokuInfinity",
                    status: 0 // 1 Online, 0 Offline
                },
                {
                    id:4,
                    name:"Shiro",
                    status: 0 // 1 Online, 0 Offline
                }
            ]
        }
    }


    iconStatus(st) {
        switch (st) {
            case 0: return ("glyphicon glyphicon-user friend-offline");
            case 1: return ("glyphicon glyphicon-user friend-online");
            default: return ("glyphicon glyphicon-user friend-offline");
        }
    }

    render() {
        //return null;
        return (
            <div>
            {this.state.list.map((value, index) => {
                return (<div>
                <a className="list-group-item">
                    <span className={this.iconStatus.call(this, value.status)} /> 
                    {value.name} 
                    <button className="btn-default">
                        <span className="glyphicon glyphicon-envelope" />
                    </button>
                    <button className="btn-default">
                        <span className="glyphicon glyphicon-menu-hamburger" />
                    </button>
                </a>
                </div>)
            })}
            <AddFriend hidden={true} />
            <ChatBox hidden={true} />
            </div>
        );
    }

    
}

export default FriendList;