import React from 'react';
import {Link} from "react-router-dom";

import '../../style/signin.css';

class RecoverPass extends React.Component {
	render() {
	    return (
	        <form className="form-signin">
				<h2 className="form-signin-heading"><center>Recover Password</center></h2>
				<div>
					<input id="email" className="form-control" type="text" placeholder="Email"/>
				</div>
				<button className="btn btn-lg btn-primary btn-block" type="button" onClick={this.recoverPass}>Okey</button>
				<Link to="/auth/login" className="btn btn-lg btn-primary btn-block">Back</Link>
	        </form>
	    );
	}
  	recoverPass() {
	  	alert('Recover Ok');
	}
}

export default RecoverPass;