import React from 'react';

class Alert extends React.Component {

	static $ = null;
	constructor(props) {
		super(props);
		this.state = {
			show: props.show,
			type: props.type,
			message: props.message,
			close: props.hasOwnProperty('close') ? props.close : true
		}
		this.$ = window.$;
	}

	UNSAFE_componentWillReceiveProps(nextProps) {
	    this.setState(
	    	{
	    		show: nextProps.show,
	    		type: nextProps.type,
	    		message: nextProps.message,
	    		close: nextProps.close,
	    	}
	    )
	}

	componentDidMount() {
		// nothing here yet
	}

	componentWillUnmount() {
	    // nothing here yet
	}

	handleAlert = () => {
		this.setState({
			show : false
		})
		this.props.handleCloseAlert(false);
	}

	render() {
		let closed = '';
		if (this.state.close === true || this.state.close === 'true') 
			closed = <button type="button" className="close" onClick={this.handleAlert}>&times;</button>
		if (this.state.show === false || this.state.show === 'false') {
			return null;
		} else {
			return (
				<div>
			        <div id="alertNova" className={"alert alert-" + this.state.type +" alert-dismissable"}>
			            {closed}
			            {this.state.message}.
			        </div>
				</div>
			);
		}
	}
}

export default Alert;